.form-container {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 500px;
    text-align: center;
    position: relative;
}

.form-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: inherit;
    color: white;
    font-size: 20px;
    font-weight: bold;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    pointer-events: none;
}

.form-overlay.show {
    opacity: 1;
    pointer-events: auto;
}

h2, h3 {
    margin: 10px 0;
    color: #333;
}

.phone-number {
    font-size: 18px;
    font-weight: bold;
    color: #0078d4;
    text-decoration: none;
    display: block;
    margin-bottom: 20px;
}

p {
    font-size: 14px;
    color: #666;
}

p a {
    color: #0078d4;
    text-decoration: none;
    font-weight: bold;
}

.form-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    gap: 3px;
}

.form-group input, 
.form-group select {
    width: 48%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

textarea {
    width: 100%;
    height: 80px;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
}

.checkbox-group {
    display: flex;
    align-items: flex-start;
    text-align: left;
    margin-bottom: 15px;
}

.checkbox-group input {
    margin-right: 10px;
}

.submit-button {
    width: 100%;
    padding: 10px;
    background: #0078d4;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.submit-button:hover {
    background: #005a9e;
}

.contact-comments {
    width: 95%;
}


/* especial styles on footer */
.toggle-comments-btn {
    background-color: #c1c1c18d;
    margin-bottom: 15px;
    color: white;
    border: none;
    padding: 8px 15px;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    margin-top: 10px;
    transition: background 0.3s ease-in-out;
}

.toggle-comments-btn:hover {
    background-color: #c1c1c1a5;
}

.comments-container {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease-in-out, opacity 0.3s ease-in-out;
    opacity: 0;
}

.comments-container.visible {
    max-height: 150px;
    opacity: 1;
    margin-top: 10px;
}
